import React, { useEffect } from "react";
import GridProduct from "./GridProduct";
import { useParams } from "react-router-dom";

const ProductCategorySection = ({ showParagraph = true, data }) => {
	const { subCat } = useParams();

	useEffect(() => {
		const element = document.getElementById(subCat);
		if (element) {
			element.scrollIntoView();
		}
	}, [subCat]);
	return (
		<div id={data.slug} className="tw-mb-6">
			{showParagraph && (
				<p className="tw-text-jt-black tw-mb-2 tw-font-bold tw-text-jt-14 jt-gln">
					{data.name}
				</p>
			)}
			<div
				style={{ gap: "1px" }}
				className="tw-grid tw-bg-gray-50 tw-rounded-md tw-grid-cols-4"
			>
				{data.products.map((each, index) => {
					return <GridProduct key={`gridProduct_${index}`} data={each} />;
				})}
			</div>
		</div>
	);
};

export default ProductCategorySection;
