import React from "react";
import { useCart } from "react-use-cart";

const CartButtonsVertical = ({ id }) => {
	const {
		addItem,
		inCart,
		isEmpty,
		cartTotal,
		totalUniqueItems,
		items,
		updateItemQuantity,
		removeItem,
		emptyCart,
	} = useCart();
	const currentItem = items.find((each) => {
		return each.id === id;
	});

	const showDecrease = currentItem.quantity > 1;
	return (
		<div
			style={{ top: "3%", right: "5%" }}
			className="tw-w-8 tw-absolute tw-self-start tw-border tw-border-jt-blue tw-shadow-elevation1 tw-rounded-md tw-flex tw-flex-col tw-overflow-hidden"
		>
			<span
				onClick={() => updateItemQuantity(id, currentItem.quantity + 1)}
				className="tw-bg-white tw-cursor-pointer tw-flex tw-justify-center tw-items-center tw-p-2"
			>
				<img
					src={require("../../../../assets/icons/add-icon.svg").default}
					className="tw-w-3"
					alt=""
				/>
			</span>
			<span className="tw-bg-jt-blue tw-text-white tw-flex tw-justify-center tw-items-center tw-p-2">
				{currentItem.quantity}
			</span>
			{!showDecrease && (
				<span
					onClick={() => removeItem(id)}
					className="tw-bg-white tw-flex tw-cursor-pointer tw-justify-center tw-items-center tw-p-2"
				>
					<img
						src={require("../../../../assets/icons/remove-icon.svg").default}
						className="tw-h-3"
						alt=""
					/>
				</span>
			)}
			{showDecrease && (
				<span
					onClick={() => updateItemQuantity(id, currentItem.quantity - 1)}
					className="tw-bg-white tw-flex tw-cursor-pointer tw-justify-center tw-items-center tw-p-2"
				>
					<img
						src={require("../../../../assets/icons/minus-icon.svg").default}
						className="tw-h-2"
						alt=""
					/>
				</span>
			)}
		</div>
	);
};

export default CartButtonsVertical;
