import React, { useState } from "react";
import AddProductModal from "./AddProductModal";
import CartButtonsVertical from "./CartButtonsVertical";
import { useCart } from "react-use-cart";

const GridProduct = ({ data }) => {
	const {
		addItem,
		inCart,
		isEmpty,
		cartTotal,
		totalUniqueItems,
		items,
		updateItemQuantity,
		removeItem,
		emptyCart,
	} = useCart();

	const { _id, sku, name, retail_price, variant_title } = data;

	const itemData = {
		id: _id,
		name,
		sku,
		price: retail_price,
		description: variant_title,
	};
	// console.log(id);

	const alreadyInCart = inCart(_id);
	const [isModalOpen, setIsModalOpen] = useState(false);

	const closeModal = () => {
		setIsModalOpen(false);
	};

	const openModal = () => {
		setIsModalOpen(true);
	};

	return (
		<div className="tw-bg-white tw-relative tw-p-3">
			<AddProductModal data={itemData} show={isModalOpen} handleClose={closeModal} />
			{alreadyInCart && <CartButtonsVertical id={_id} />}
			{!alreadyInCart && (
				<div
					onClick={() => {
						addItem(itemData);
					}}
					style={{ top: "5%", right: "5%" }}
					className="tw-h-5 tw-w-5 tw-shadow tw-absolute tw-cursor-pointer tw-rounded  tw-flex tw-items-center tw-justify-center"
				>
					<img
						src={require("../../../../assets/icons/add-icon.svg").default}
						className="tw-w-3"
						alt=""
					/>
				</div>
			)}
			<img
				onClick={openModal}
				src={require("../../../../assets/images/three-crowns.svg").default}
				className="tw-h-32 tw-cursor-pointer tw-block tw-mx-auto"
				alt=""
			/>
			<div className="tw-text-center">
				<p className="tw-text-jt-blue jt-glb tw-mt-3">{retail_price}</p>
				<p className="tw-text-jt-black tw-h-10 tw-overflow-hidden jt-gln tw-mt-3">{name}</p>
				<p className="tw-text-gray-400 jt-gln tw-mt-3">{sku}g</p>
			</div>
		</div>
	);
};

export default GridProduct;
