import React, { useState } from "react";
import { Modal, Button } from "react-bootstrap";
import CartButtons from "./CartButtons";
import { useCart } from "react-use-cart";

const AddProductModal = ({ show, handleClose, data }) => {
	const {
		addItem,
		inCart,
		isEmpty,
		cartTotal,
		totalUniqueItems,
		items,
		updateItemQuantity,
		removeItem,
		emptyCart,
	} = useCart();
	const { id, description, price, weight, name } = data;
	const alreadyInCart = inCart(data.id);
	const [quantity, setQuantity] = useState(() => {
		if (alreadyInCart) {
			const currentItem = items.find((each) => {
				return each.id === id;
			});
			return currentItem.quantity;
		} else {
			return 1;
		}
	});

	const incrementQuantity = () => {
		setQuantity((prev) => prev + 1);
	};

	const decrementQuantity = () => {
		setQuantity((prev) => {
			if (prev === 1) {
				return prev;
			} else {
				return prev - 1;
			}
		});
	};

	const addToBasket = () => {
		addItem(data, quantity);
	};

	const updateBasket = () => {
		updateItemQuantity(data.id, quantity);
	};

	return (
		<Modal size="md" show={show} onHide={handleClose}>
			<Modal.Body style={{ padding: "0px" }}>
				<div className="tw-py-3 ">
					<div className="tw-border-b tw-px-5 tw-py-3 tw-border-gray-100">
						<div className="tw-flex tw-justify-between tw-items-center">
							<div className="tw-flex tw-cursor-pointer tw-items-center">
								<img
									src={require("../../../../assets/icons/heart-icon.svg").default}
									className="w-4"
									alt=""
								/>
								<span className="tw-text-jt-12 jt-glb tw-ml-2 tw-text-jt-black">
									Add to Favourite
								</span>
							</div>
							<img
								onClick={handleClose}
								src={
									require("../../../../assets/icons/modal-close-icon.svg").default
								}
								className="tw-w-6 tw-cursor-pointer"
								alt=""
							/>
						</div>
					</div>
					<div className="tw-py-4 tw-border-b tw-border-gray-100">
						<img
							src={require("../../../../assets/images/three-crowns.svg").default}
							className="tw-h-60  tw-mx-auto tw-block"
							alt=""
						/>
					</div>
					<div className="tw-py-4 tw-px-5 tw-border-b tw-border-gray-100">
						<p className="tw-mb-2 jt-glb tw-text-jt-18 tw-text-jt-black">{name}</p>
						<span className="tw-block jt-gln tw-text-jt-gray1">450g</span>
						<div className="tw-mt-4">
							<p className="tw-mb-2 tw-text-jt-gray1 jt-gln tw-text-jt-12">
								{description}
							</p>
						</div>
					</div>
					<div className="tw-py-4 tw-px-5 tw-flex tw-justify-between tw-items-center">
						<CartButtons
							handleIncrement={incrementQuantity}
							handleDecrement={decrementQuantity}
							quantity={quantity}
						/>
						<div className="tw-flex tw-w-2/5 tw-border tw-border-jt-blue tw-rounded-md tw-overflow-hidden">
							{!alreadyInCart && (
								<button
									onClick={addToBasket}
									className="focus:tw-outline-none tw-w-3/5 tw-py-2 tw-flex tw-justify-center tw-items-center tw-bg-jt-blue tw-text-white jt-glb"
								>
									Add To Basket
								</button>
							)}
							{alreadyInCart && (
								<button
									onClick={updateBasket}
									className="focus:tw-outline-none tw-w-3/5 tw-py-2 tw-flex tw-justify-center tw-items-center tw-bg-jt-blue tw-text-white jt-glb"
								>
									Update Basket
								</button>
							)}

							<span className="tw-py-2 tw-w-2/5 tw-flex tw-items-center tw-justify-center tw-text-jt-blue jt-glb">
								{quantity * data.price}
							</span>

							{/* {!alreadyInCart && (
								<span className="tw-py-2 tw-w-2/5 tw-flex tw-items-center tw-justify-center tw-text-jt-blue jt-glb">
									{quantity * data.price}
								</span>
							)} */}
						</div>
					</div>
				</div>
			</Modal.Body>
		</Modal>
	);
};

export default AddProductModal;
