import React, {useState, useHistory,} from 'react';
import { useForm } from "react-hook-form";
import { Button, Modal } from "react-bootstrap";
import './EditProfile.css';
import store from '../../../../redux/Store';


export default function EditProfile({showForm, showEditForm, hideEditForm}) {
    
        const [Name, setName] = useState();
        const [Email, setEmail] = useState();
        const [phoneNumber, setPhone] = useState();
        
        const handleClose = () => {
            hideEditForm();
        };

        const {
            register,
            handleSubmit,
            watch,
            formState: { errors },
          } = useForm({mode: 'all'});
         
        

        const handleName =(event)=>{
              setName(event.target.value);
        }

        const handleEmail =(event)=>{
              setEmail(event.target.value);
        }

        const handlePhone =(event)=>{
              setPhone(event.target.value);
      }
         

        const onSubmit = async data => {
              console.log(data);
              //updte data to database
          }

    return (
        <>
            <Modal show={showForm} onHide={handleClose}  animation={true} >
                <Modal.Header  className="border-0">
                <div></div>
                <h6 className="modal__header">Update Account Information</h6>
                <img src={require("../../../../assets/icons/icon-cancel.svg").default}
					onClick={handleClose}
					className="tw-w-6"
					style={{cursor:'pointer'}}
					alt=""
					/>
                </Modal.Header>
                <Modal.Body>
                    <form onSubmit={handleSubmit(onSubmit)} className="edit__profile__form">
                      <div className="form-group">
                      <input
                            type="text"
                            name="Name"
                            {...register("Name", { required: true})}
                            onChange={handleName}
                            id="name"
                            className={
                                "px-3 form-control form-field " +
                                (errors.Name ? "error" : " ")
                            }
                        />
                        {errors.Name && errors.Name.type === "required" && (
                                <span className="errorMessage text-right">
                                  Name is required
                                </span>
                            )}
                      </div>

                      <div className="form-group">
                      <input
                            type="email"
                            name="Email"
                            {...register("Email", {required: true, pattern: /^\S+@\S+$/i})}
                            onChange={handleEmail}
                            id="email"
                            className={
                                "px-3 form-control form-field " +
                                (errors.Email ? "error" : " ")
                            }
                        />
                        {errors.Email && errors.Email.type === "required" && <span className="errorMessage">Email is required</span>}
						{errors.Email && errors.Email.type === "pattern" && <span className="errorMessage">Incorrect email format</span>}
                      </div>
                      <div className="form-group">
                        <input
                                type="text"
                                name="phoneNumber"
                                {...register('phoneNumber', {required: true,  minLength: 11, maxLength:11, pattern:/^[+-]?\d*(?:[.,]\d*)?$/})}
                                onChange={handlePhone}
                                readOnly={true}
                                id="phone"	
                                className={"px-3 form-control form-field " + (errors.phone ? "error" : " ")}  
                            />
                        {errors.phone && errors.phone.type === "required" && <span className="errorMessage">Phone number is required</span>}
                        {errors.phone && errors.phone.type === "minLength" && <span className="errorMessage">Incorrect phone number,</span> }
                        {errors.phone && errors.phone.type === "maxLength" && <span className="errorMessage">Incorrect phone number</span> }
                        {errors.phone && errors.phone.type === "pattern" && <span className="errorMessage">Valid phone number is required</span> } 
                      </div>
                      <div className="col-12 form-group">
                            <button
                                type="submit"
                                className="submit form-control btn-primary"
                            >
                                Save
                            </button>
					 </div>
                    </form>
                </Modal.Body>
                
            </Modal> 
            
        </>
    )
}
