import React from "react";
import { Route, Redirect } from "react-router-dom";
import { connect } from "react-redux";

const PublicGuard = ({ children, category, ...rest }) => {
	return (
		<Route
			{...rest}
			render={(props) => {
				const token = localStorage.getItem("JT_TKN");
				const url = `${category[0].slug}_${category[0]._id}/${category[0].children[0].slug}`;
				return !token ? children : <Redirect to={`/home/category/${url}`} />;
			}}
		/>
	);
};

const mapStateToProps = (state) => {
	return { category: state.category.categories };
};

export default connect(mapStateToProps, null)(PublicGuard);
