import React, { useState, useRef, useEffect } from "react";
import { useForm } from "react-hook-form";
import ReactFlagsSelect from 'react-flags-select';
import axios from "axios";
import bag from "./../img/bag.svg";
import man from "./../img/man.svg";
import herojeetarlogo from "./../img/herojeetarlogo.svg";
import continueRule from "./../logo/continueRule.svg";
import goods from "./../img/goods right.svg";
import facebook from "./../logo/facebook.svg";
import google from "./../logo/google.svg";
import OTP from "./OTP";

import PasswordModal from "./PasswordModal";
import "../component/Hero.css";
import "../component/Form.css";
import styled from "styled-components";
import Loader from "react-js-loader";


const Herologo = styled.img`
  margin-left: 100px;
  margin-top: 76px;
  @media (max-width: 1024yarn px) {
    margin-left: 5px;
  }
`;

/* 
  axios.defaults.headers = {
  'Content-Type': 'application/json',
  'simulate_prod': true
}
*/

const Hero = () => {

const {
  register,
  handleSubmit,
  watch,
  formState: { errors },
} = useForm({ mode: "all" });

const [selected, setSelected] = useState('NG');

const phones = {
        US: '+1',
        DE: '+50',
        TR: '+90',
        IT: '+7',
        IN: '+15'
    }


const [showOTP, handleShowOTP] = useState(false)
const openOTP =()=>{handleShowOTP(true)}	
const closeOTP =()=>{handleShowOTP(false)}


const [showPasswordModal, handleShowPasswordModal] = useState(false)
const openPasswordModal =()=>{handleShowPasswordModal(true)}	
const closePasswordModal =()=>{handleShowPasswordModal(false)}



const [phoneNumber, setphoneNumber] = useState(null);
const [token, setToken] = useState(null);
const [message, setMessage]=useState(null)
const [loading, setLoading]=useState(false)



useEffect(() => {
  //console.log(phoneNumber, token);
  if(phoneNumber){
    registerUser();
  }
}, [phoneNumber])

 const registerUser = async () =>{
   setLoading(true)
    try{

      await axios.post(`https://jeetar-eqtxqyoupa-nw.a.run.app/api/v1/auth/register`, { phone_number: phoneNumber})
      .then(res => {
                setToken(res.data.data.temp_token);
                setLoading(false)
                openOTP();
                console.log(res.data)
      }).catch(
        error =>{
          setLoading(false);
          if(error.response){
            setToken(null);
            const status =error.response.data.status;
            if(error.response.data.error.temp_token){
              setToken(error.response.data.error.temp_token);
              openOTP();
            }
            else{
              openPasswordModal();
            }
            
             console.log(error.response.data)
          }
        }
        
        
        );
    }
    catch(err){
      setLoading(false)
      console.log(err);
    }
  }

  const onSubmit = async (data) => { 
    if (data) {
      let phone = "234" + data.phone_number.substring(1);
      setphoneNumber(null);
      setphoneNumber(phone); 
      
      // setTimeout(() => {
      //   registerUser();  
      // }, 5000);
    }
  };

  return (
    <>
      <OTP status ={showOTP} close={closeOTP} open={openOTP} phoneNumber ={phoneNumber} token ={token} message={message} />
      <PasswordModal status ={showPasswordModal} close={closePasswordModal} open={openPasswordModal} phoneNumber ={phoneNumber} token ={token} message={message} /> 
      <div className="hero-section" style={{pointerEvents : loading ? 'none' : 'all'}}>
        <div className="row">
          <div className="col-sm-12 col-md-7 col-lg-7 d-none d-sm-block">
            <div className="container hero-left">
              <Herologo
                src={herojeetarlogo}
                alt="logo"
                className="mb-4 herologo"
              />
              <h1 className="fast-shoping">
                Fast Shopping, <br></br>Fast Shipping in Best Time
              </h1>
              <div className="image-wrapper tw-flex">
                <img
                  src={bag}
                  alt="ilustration1"
                  className="ilustration1 img-fluid"
                />
                <img
                  src={man}
                  alt="ilustration2"
                  className="ilustration2 img-fluid"
                />
              </div>
            </div>
          </div>
          <div className="col-sm-12 col-md-5 col-lg-5 hero-right">
            <div className="form-panel">
              <form onSubmit={handleSubmit(onSubmit)}>
                <div className="container">
                  <div className="form-group">
                    <h5 className="text-center py-2 login__or__register">
                      Login or Register
                    </h5>
                  </div>
                  <div className="row g-2">
                    <div className="col-3">
                      <select
                        name="countryCode"
                        className={"form-select form-field"}
                      >
                        <option value="+234">+234</option>
                      </select>
                    </div>
                    <div className="col-9">
                      <div className="form-floating">
                        <input
                          type="tel"
                          name="phone_number"
                          {...register("phone_number", {
                            required: true,
                            minLength: 11,
                            maxLength: 11,
                            pattern:/^[+-]?\d*(?:[.,]\d*)?$/,
                          })}
                          id="phone"
                          className={
                            "form-control form-field " +
                            (errors.phone_number? "error" : " ")
                          }
                          placeholder="Phone Number"
                        />
                        {errors.phone_number && errors.phone_number.type === "required" && (
                          <span className="errorMessage">
                            Phone number is required
                          </span>
                        )}
                        {errors.phone_number && errors.phone_number.type === "minLength" && (
                          <span className="errorMessage">
                            Incorrect phone number,
                          </span>
                        )}
                        {errors.phone_number && errors.phone_number.type === "maxLength" && (
                          <span className="errorMessage">
                            Incorrect phone number
                          </span>
                        )}
                        {errors.phone_number && errors.phone_number.type === "pattern" && <span className="errorMessage">Valid phone number is required</span> }

                        <label for="floatingInput">Phone Number</label>
                      </div>
                    </div>
                    <div className="col-12 form-group">
                      <button
                        type="submit"
                        className="continue-with-phone form-control btn-primary"
                      >
                        Continue With Phone Number
                      </button>
                    </div>
                    <div className="col-12 form-group mt-n5">
                      <img
                        src={continueRule}
                        alt="continue"
                        className="d-none d-sm-block mx-auto"
                      />
                    </div>
                    <div className="col-12 d-flex justify-content-center form-group text-center mt-0">
                      <a href="#">
                        <img src={google} className="mx-3" alt="google" />
                      </a>
                      <a href="#">
                        <img src={facebook} className=" mx-3" alt="facebook" />
                      </a>
                    </div>
                  </div>
                </div>
              </form>
            </div>

            <img src={goods} alt="goods" className="goods" />
          </div>
        </div>
      </div>
      {loading ?
      <div className={"row"} style={{position: 'fixed', top: '50%', left: '50%', }}>
            <div className={"item"}>
                <Loader type="box-up" bgColor={"#FFFFFF"} size={100} />
            </div>
        	</div>
        : null}
    </>
  );
};
export default Hero;
