import React, { useState } from "react";
import {Link} from 'react-router-dom'
import './ProfileMenu.css';
const ProfileMenu = ({ activeFunc, value, active, menuName, menuPath }) => {
	
	return (
		<div className="">
			<div
				onClick={() => {
					activeFunc(value);
				}}
				className={`${
					(active === value) && (menuPath===window.location.pathname) ? "tw-bg-blue-200" : ""
				} tw-flex tw-px-3 tw-items-center tw-py-2`}
				style={{cursor:'pointer'}}
			>
				<div className="tw-flex tw-items-center tw-justify-center">
					<div>
					  <Link to={menuPath} className="tw-ml-2 menu__name menu__link">{menuName}</Link>
					</div>	
				</div>
				
			</div>
		
		</div>
	);
};

export default ProfileMenu;
