import React, { useEffect } from "react";
import "./stylesheet/index.scss";
import "./Tailwind/output.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { connect } from "react-redux";

import "./App.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { CartProvider } from "react-use-cart";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Navbar from "./component/Navbar";
import Footer from "./component/Footer";
import Default from "./component/Default";
import Home from "./pages/Home";
import Jeetarbusiness from "./pages/Jeetarbusiness";
import Jeetarhealth from "./pages/Jeetarhealth";
import Routes from "./Routes/Routes";
import { useQuery } from "react-query";
import { setCategory } from "./redux/Actions/category";
import { makeCategoryRequest } from "./Services/requests/categories";

function App({ setCat, category }) {
	const apiFunction = async () => {
		const response = await makeCategoryRequest();
		return response.data;
	};
	useQuery("category", apiFunction, {
		onSuccess: (data) => {
			if (data) {
				console.log(data.data);
				setCat(data.data);
			}
		},
		refetchOnWindowFocus: false,
		refetchOnMount: false,
	});

	const hasCategory = category.length > 0;
	if (!hasCategory) {
		return (
			<div className="tw-w-screen tw-h-screen tw-bg-white tw-flex tw-justify-center tw-items-center">
				<div className="tw-w-30vw">
					<img
						src={require("./assets/images/bouncingBall.gif").default}
						className="mx-auto"
						alt=""
					/>
					<h4 className="tw-text-3xl jt-pdb tw-text-jt-blue tw-text-center">jeetar</h4>
				</div>
			</div>
		);
	}

	return (
		<>
			<CartProvider>
				<Routes />
			</CartProvider>

			{/* <Router>
				<Navbar />
				<Switch>
					<Route exact path="/" component={Home} />
					<Route path="/jeetarbusiness" component={Jeetarbusiness} />
					<Route path="/jeetarhealth" component={Jeetarhealth} />
					<Route component={Default} />
				</Switch>
				<Footer />
			</Router> */}
		</>
	);
}

const mapStateToProps = (state) => {
	return { category: state.category.categories };
};

const mapDispatchToProps = (dispatch) => {
	return {
		setCat: (value) => {
			dispatch(setCategory(value));
		},
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
