import React, { useRef } from "react";
import { useForm } from "react-hook-form";
import './ChangePassword.css'

  // Fetch Previous password and Compare with the input;
  
  

const ChangePassword =()=> {
    const {
        register,
        handleSubmit,
        watch,
        formState: { errors },
      } = useForm({mode: 'all'});
      const password = useRef({});
      password.current = watch("password", "");

      const onSubmit = async data => {
        data.phone = '234'+data.phone;
        console.log(data);
    }
    
    return (
        <>
          	<form onSubmit={handleSubmit(onSubmit)} className="col-sm-8 py-5 tw-mx-auto">
              <div className="row">
              <div className="col-12 form-group">	
                        <div className="form-floating">
                            <input
                                type="password"
                                name="oldpassword"
                                {...register('oldpassword', 
                                    {
                                    required: true, 
                                    minLength:7,
                                    pattern: '', 
                                        
                                    })}
                                    className={"form-control form-field " + (errors.oldpassword ? "error" : " ")}  
                                placeholder="oldpassword"
                            />
                            
                            {errors.oldpassword && errors.newpassword.type === "required" && <span className="errorMessage">Previous Password</span>}
                            {errors.oldpassword && errors.newpassword.type === "minLength" && <span className="errorMessage">Password must be at least 7 long</span>}
                            {errors.oldpassword && errors.newpassword.type === "pattern" && <span className="errorMessage">Password must contain at least one special character.</span>}
                            <label for="floatingInput">Previous Password</label>

                        </div>
                </div>
                <hr  className="tw-bg-gray-300 my-2"/>	
                <div className="col-12 form-group">	
                        <div className="form-floating">
                            <input
                                type="password"
                                name="newpassword"
                                {...register('newpassword', 
                                    {
                                    required: true, 
                                    minLength:7,
                                    pattern: '', 
                                        
                                    })}
                                    className={"form-control form-field " + (errors.newpassword ? "error" : " ")}  
                                placeholder="Password"
                            />
                            
                            {errors.newpassword && errors.newpassword.type === "required" && <span className="errorMessage">New Password </span>}
                            {errors.newpassword && errors.newpassword.type === "minLength" && <span className="errorMessage">Password must be at least 7 long</span>}
                            {errors.newpassword && errors.newpassword.type === "pattern" && <span className="errorMessage">Password must contain at least one special character.</span>}
                            <label for="floatingInput">New Password (Min 7 characters)</label>

                        </div>
                    </div>
                </div>
                <div className="col-12 form-group">
                    <div className="form-floating">
                        <input
                            type="password"
                            name="renewpassword"
                            {...register('renewpassword',{
                                required:true,
                                validate: value => value === password.current,

                            })}
                            
                            className={"form-control form-field " + (errors.renewpassword ? "error" : " ")}  		
                            placeholder="Re-Type Password"
                        />
                        {errors.renewpassword && errors.renewpassword.type === "required" && <span className="errorMessage">Confirm Repeat New Password</span>}
                        {errors.renewpassword && errors.renewpassword.type === "validate" && <span className="errorMessage">Password does not match</span>}
                        <label for="floatingInput">Repeat New Password</label>
                    </div>
                </div>
                <div className="col-12 form-group">
                    <button
                        type="submit"
                        className="submit form-control btn-primary"
                    >
                        Change Password
                    </button>
                </div>
            </form>  
        </>
    )
}

export default ChangePassword
