import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import PrivateGuard from "../Guards/PrivateGuard";
import PublicGuard from "../Guards/PublicGuard";
import Home from "../pages/Home";
import Jeetarbusiness from "../pages/Jeetarbusiness";
import Jeetarhealth from "../pages/Jeetarhealth";
import PrivateRoutes, { privateRouteData } from "./PrivateRoutes";
import PublicRoutes, { publicRouteData } from "./PublicRoutes";

const privatePaths = privateRouteData.map((each) => {
	return each.path;
});

const publicPaths = publicRouteData.map((each) => {
	return each.path;
});

const Routes = () => {
	return (
		<Router>
			<Switch>
				<PrivateGuard path={privatePaths}>
					<PrivateRoutes />
				</PrivateGuard>

				<PublicGuard path={publicPaths}>
					<PublicRoutes />
				</PublicGuard>
			</Switch>
		</Router>
	);
};

export default Routes;
