import React from "react";
import { Route, Switch } from "react-router-dom";
import OrderHome from "../component/OrderHome";
import PublicLayout from "../Layout/PublicLayout";
import Home from "../pages/Home";
import Jeetarbusiness from "../pages/Jeetarbusiness";
import Jeetarhealth from "../pages/Jeetarhealth";

export const publicRouteData = [
	{
		path: "/",
		exact: true,
		component: Home,
	},
	{
		path: "/jeetarbusiness",
		exact: true,
		component: Jeetarbusiness,
	},
	{
		path: "/jeetarhealth",
		exact: true,
		component: Jeetarhealth,
	},
];

const PublicRoutes = () => {
	return (
		<PublicLayout>
			<switch>
				{publicRouteData.map((each) => {
					return <Route path={each.path} exact={each.exact} component={each.component} />;
				})}
			</switch>
		</PublicLayout>
	);
};

export default PublicRoutes;
