import React from "react";
import { Route, Redirect } from "react-router-dom";

const PrivateGuard = ({ children, ...rest }) => {
	return (
		<Route
			{...rest}
			render={(props) => {
				return localStorage.getItem("JT_TKN") ? children : <Redirect to="/" />;
			}}
		/>
	);
};

export default PrivateGuard;
