import React from "react";
import { useCart } from "react-use-cart";

const CartItem = ({ data }) => {
	const {
		addItem,
		inCart,
		isEmpty,
		cartTotal,
		totalUniqueItems,
		items,
		updateItemQuantity,
		removeItem,
		emptyCart,
	} = useCart();
	const { id, name, quantity, price, sku } = data;

	const showDecrease = quantity > 1;

	return (
		<div className="tw-flex tw-justify-between">
			<div>
				<p className="tw-text-jt-black jt-gln tw-mb-0 tw-mt-0">{name}</p>
				<p className="tw-text-gray-400 jt-gln tw-mb-0 tw-mt-2">{sku}g</p>
				<p className="tw-text-jt-blue jt-glb tw-mt-2">{price}</p>
			</div>

			<div className="tw-w-28 tw-self-start tw-border tw-border-jt-blue tw-shadow-elevation1 tw-rounded-md tw-grid tw-grid-cols-3 tw-overflow-hidden">
				{!showDecrease && (
					<span
						onClick={() => removeItem(id)}
						className="tw-bg-white tw-flex tw-cursor-pointer tw-justify-center tw-items-center tw-p-2"
					>
						<img
							src={require("../../../../assets/icons/remove-icon.svg").default}
							className="tw-h-4"
							alt=""
						/>
					</span>
				)}
				{showDecrease && (
					<span
						onClick={() => updateItemQuantity(id, quantity - 1)}
						className="tw-bg-white tw-flex tw-cursor-pointer tw-justify-center tw-items-center tw-p-2"
					>
						<img
							src={require("../../../../assets/icons/minus-icon.svg").default}
							className="tw-h-2"
							alt=""
						/>
					</span>
				)}
				<span className="tw-bg-jt-blue tw-text-white tw-flex tw-justify-center tw-items-center tw-p-2">
					{quantity}
				</span>
				<span
					onClick={() => updateItemQuantity(id, quantity + 1)}
					className="tw-bg-white tw-cursor-pointer tw-flex tw-justify-center tw-items-center tw-p-2"
				>
					<img
						src={require("../../../../assets/icons/add-icon.svg").default}
						className="tw-w-3"
						alt=""
					/>
				</span>
			</div>
		</div>
	);
};

export default CartItem;
