import React,{Component} from "react";
import {InfoWindow, Map, Marker, GoogleApiWrapper} from 'google-maps-react';
import PlacesAutocomplete, { geocodeByAddress, geocodeByPlaceId, getLatLng, } from 'react-places-autocomplete';
import './GoogleMap.css';


const containerStyle = {  
  width: '98%',
  height: '50vh',
  padding:'100px'}


export class MapContainer extends Component {
  constructor(props) {
    super(props);
    this.state = { address: '' ,
                  showingInfoWindow: false,
                  activeMarker: {},
                  selectedPlace: {},
                  mapCenter: {
                    // Lagos, Default location
                    lat: '6.5244',
                    lng: '3.3792',
          }
    };
  }
 
    
    handleChange = address => {
      this.setState({ address });
    };
   
    handleSelect = address => {
      geocodeByAddress(address)
        .then(results => getLatLng(results[0]))
        .then(latLng => {
          console.log('Success', latLng)
          this.setState({address})
          this.setState({mapCenter:latLng})}
          )
        .catch(error => console.error('Error', error));
    };
   
   
    onMarkerClick = (props, marker, e) =>
      this.setState({
        selectedPlace: props,
        activeMarker: marker,
        showingInfoWindow: true
      });
   
    onMapClicked = (props) => {
      if (this.state.showingInfoWindow) {
        this.setState({
          showingInfoWindow: false,
          activeMarker: null
        })
      }
    };
   
    render() {
      return (
        <>
          <div className="position-relative py-100">
            
          <div className="search_places tw-flex tw-p-3 tw-mx-auto">
              <div className="flex-1 tw-p-1">
                  <img src={require("../../../../assets/icons/icon-search.svg").default}
                    className=""
                    style={{cursor:'pointer'}}
                    alt=""
                    />
              </div>
              <div className="tw-flex-auto mx-5">
              <PlacesAutocomplete
                      value={this.state.address}
                      onChange={this.handleChange}
                      onSelect={this.handleSelect}
                    >
                      {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
                        <div>
                          <input
                            {...getInputProps({
                              placeholder: 'Search Places ...',
                              className: 'location-search-input',
                            })}
                          />
                          <div className="autocomplete-dropdown-container">
                            {loading && <div>Loading...</div>}
                            {suggestions.map(suggestion => {
                              const className = suggestion.active
                                ? 'suggestion-item--active'
                                : 'suggestion-item';
                              // inline style for demonstration purpose
                              const style = suggestion.active
                                ? { backgroundColor: '#fafafa', cursor: 'pointer' }
                                : { backgroundColor: '#ffffff', cursor: 'pointer' };
                              return (
                                <div
                                  {...getSuggestionItemProps(suggestion, {
                                    className,
                                    style,
                                  })}
                                >
                                  <span>{suggestion.description}</span>
                                </div>
                              );
                            })}
                          </div>
                        </div>
                      )}
            </PlacesAutocomplete>
              </div>     
             <div className="flex-1">
                <div className="d-flex p-1 location tw-align-center">
                   <img src={require("../../../../assets/icons/icon-location.svg").default}
                    className=""
                    style={{cursor:'pointer'}}
                    alt=""
                    />
                    <p className="tw-mx-3">Find my location</p>
                </div>
             </div>                
            </div>
         
          <div className="googlemap tw-p-2">
              <Map google={this.props.google}
                initialCenter={{
                  lat:this.state.mapCenter.lat,
                  lng:this.state.mapCenter.lng,
                }}
                center={{
                  lat:this.state.mapCenter.lat,
                  lng:this.state.mapCenter.lng,
                }}
                containerStyle={containerStyle}
            
            >
              <Marker onClick={this.onMarkerClick}
                      name={'Current location'}
                      position={{lat:this.state.mapCenter.lat,
                        lng:this.state.mapCenter.lng,}} 
                      />

            <InfoWindow onClose={this.onInfoWindowClose}>
                <div>
                  <h1>{this.state.selectedPlace.name}</h1>
                </div>
            </InfoWindow>
        </Map>

          </div>
       
          </div>
          <div className="">
            
          </div>
        </>
        
      )
    }
}

export default GoogleApiWrapper({
     apiKey: ('AIzaSyCcNdMhcs5Rhy2lSD9jr49ltHe_cfeDp0A')
  })(MapContainer)