import React from "react";
import { Route, Switch } from "react-router-dom";
import LoggedInLayout from "../Layout/LoggedInLayout";
import Basket from "../pages/LoggedInPages/Basket/Basket";
import LoggedInHome from "../pages/LoggedInPages/LoggedInHome/LoggedInHome";
import Order from "../pages/LoggedInPages/Order/Order";
import AddressMap from "../pages/LoggedInPages/Address/AddressMap";
import Profile from "../pages/LoggedInPages/Profile/Profile";
import Address from "../pages/LoggedInPages/Address/Address";
import Favourite from "../pages/LoggedInPages/Favourite/Favourite";
import PreviousOrder from "../pages/LoggedInPages/PreviousOrder/PreviousOrder";
import Payment from "../pages/LoggedInPages/Payment/Payment";
import Password from "../pages/LoggedInPages/Password/Password";
import Preference from "../pages/LoggedInPages/preference/Preference";

export const privateRouteData = [
	{
		path: "/home/order",
		exact: true,
		component: Order,
	},
	{
		path: "/home/category/:cat/:subCat",
		exact: true,
		component: LoggedInHome,
	},
	{
		path: "/home/cart",
		exact: true,
		component: Basket,
	},

	{
		path: "/home/profile",
		exact: true,
		component: Profile,
	},

	{
		path: "/home/profile/address",
		exact: true,
		component: Address,
	},

	{
		path: "/home/profile/address/map",
		exact: true,
		component: AddressMap,
	},
	{
		path: "/home/profile/favourite",
		exact: true,
		component: Favourite,
	},
	{
		path: "/home/profile/previous-order",
		exact: true,
		component: PreviousOrder,
	},
	{
		path: "/home/profile/payment",
		exact: true,
		component: Payment,
	},

	{
		path: "/home/profile/password",
		exact: true,
		component: Password,
	},
	{
		path: "/home/profile/preference",
		exact: true,
		component: Preference,
	},
];

const PrivateRoutes = () => {
	return (
		<LoggedInLayout>
			<Switch>
				{privateRouteData.map((each, index) => {
					return (
						<Route
							key={`privateRoutes_${index}`}
							path={each.path}
							exact={each.exact}
							component={each.component}
						/>
					);
				})}
			</Switch>
		</LoggedInLayout>
	);
};

export default PrivateRoutes;
