import React, { useState, useEffect } from "react";
import { useHistory, useParams, generatePath } from "react-router-dom";

import AnimateHeight from "react-animate-height";

const CategoryMenu = ({ showSubMenus = true, activeFunc, close, value, active, data }) => {
	const history = useHistory();
	const { cat, subCat } = useParams();
	const _cat = cat.split("_")[0];

	const { name, slug, children, icon, _id } = data;
	const [isOpen, setIsOpen] = useState(() => {
		return _cat === slug;
	});

	const shouldOpen = _cat === slug && isOpen;

	const handleSubClick = (value) => {
		const url = generatePath("/home/category/:cat/:subCat", {
			cat: `${slug}_${_id}`,
			subCat: value,
		});
		history.push(url);
	};

	const handleClick = () => {
		if (shouldOpen) {
			setIsOpen((prev) => false);
		} else {
			setIsOpen((prev) => true);
		}
		const url = generatePath("/home/category/:cat/:subCat", {
			cat: `${slug}_${_id}`,
			subCat: children[0].slug,
		});
		history.push(url);
	};

	return (
		<div className="">
			<div
				onClick={handleClick}
				className={`${
					_cat === slug ? "tw-bg-blue-100" : ""
				} tw-flex tw-px-3 tw-items-center tw-cursor-pointer tw-py-1`}
			>
				<div className="tw-flex tw-items-center">
					{/* <div style={{ width: "51px", height: "51px" }}> */}
					<img
						src={icon}
						// src={require("../../../../assets/images/new-products-icon.svg").default}
						className=" tw-h-8 tw-w-8 tw-overflow-hidden tw-object-cover"
						alt=""
					/>
					{/* </div> */}
					<span className="tw-ml-5 tw-font-bold">{name}</span>
				</div>
				<img
					src={require("../../../../assets/icons/chevron-up-blue.svg").default}
					className={`${
						_cat === slug ? "tw-rotate-180" : "tw-rotate-0"
					} tw-w-3 tw-ml-auto tw-cursor-pointer tw-transform tw-transition-transform`}
					alt=""
				/>
			</div>
			<AnimateHeight
				id="example-panel"
				duration={500}
				height={shouldOpen ? "auto" : 0} // see props documentation below
			>
				<ul
					className="tw-pl-0 tw-mb-0"
					// style={{ transitionProperty: "height" }}
					// className={`${
					// 	!shouldOpen ? "jt-animate-height-initial" : "jt-animate-height-end"
					// } tw-duration-300 tw-overflow-hidden `}
					// className={`${
					// 	!shouldOpen ? "tw-hidden" : "tw-block"
					// } tw-duration-300 tw-overflow-hidden tw-pl-0`}
				>
					{children.map((each, index) => {
						return (
							<li
								key={`categoryMenu_${index}`}
								onClick={() => {
									handleSubClick(each.slug);
								}}
								className={`${
									subCat === each.slug ? "tw-bg-gray-100" : ""
								} tw-py-2 tw-cursor-pointer tw-pl-20`}
							>
								{each.name}
							</li>
						);
					})}
				</ul>
			</AnimateHeight>
		</div>
	);
};

export default CategoryMenu;
