import React, { useRef, useState } from "react";
import { Modal } from 'react-bootstrap';
import textlogo from "./../logo/textlogo.svg";
import continueRule from "./../logo/continueRule.svg";
import facebook from "./../logo/facebook.svg";
import google from "./../logo/google.svg";
import cancel from "./../logo/cancel.svg";
import { useForm } from "react-hook-form";
import axios from "axios";
import { useHistory, Redirect, Route } from "react-router";
import PasswordModal from "./PasswordModal";
import { data } from "autoprefixer";

const signupclose ={
	position: 'absolute',
    right: '12px',
    top: '12px'
}
       

    const SignUp = (props) => {
	const {
		register,
		handleSubmit,
		watch,
		formState: { errors },
	  } = useForm({mode: 'all'});

	const [showPasswordModal, handleShowPasswordModal] = useState(false)
	const openPasswordModal =()=>{handleShowPasswordModal(true)}	
	const closePasswordModal =()=>{handleShowPasswordModal(false)}

	const password = useRef({});
      password.current = watch("password", "");
	  
    const handleClose = () => {
		props.close();
	};

	const [message, setMessage]=useState(null);
	const history = useHistory();

	const Editprofile = async (data) =>{
		try{
	
		  await axios.post(`https://jeetar-eqtxqyoupa-nw.a.run.app/api/v1/auth/account-setup`, {
			"temp_token": props.token,
			"first_name": data.fullname,
			"last_name": data.fullname,
			"phone_number": data.phone_number,
			"email": data.email,
			"gender": "MALE",
			"date_of_birth": "2019-09-26",
			"password": data.password

		  })
		  .then(res => {
			         props.close();
					console.log(res.data.message)
					//setToken(res.data.data.temp_token);
					 openPasswordModal();
					// redirect to login
		  }).catch(error => {
			if (error.response) {
			  // The request was made and the server responded with a status code
			  // that falls out of the range of 2xx
			  console.log(error.response.data.message);
			  setMessage(error.response.data.message);
			  //setToken(error.response.data.error.temp_token);
			  
			}
			  });
		}
		catch(err){
		  console.log(err);
		}
	  }
	
	

	const onSubmit = async data => {
		  data.phone_number = "234" + data.phone_number.substring(1);
		  Editprofile(data);
		  
	  }
	
		return (
			<>  
			    <PasswordModal status ={showPasswordModal} close={closePasswordModal} open={openPasswordModal} phoneNumber ={props.phoneNumber} token={props.token} message={props.message} /> 
				<Modal show ={props.status} className="pt-0">
					<div className="modal-dialog">
						<div className="modal-content container">
							<div className="mt-3 border-0">
								<div className="logo-wrapper">
									<div className="textlogo px-3 me-5">
										<img src={textlogo} alt="jeetar logo" />
									</div>
									<div className="already-sign-in-wrapper ms-5">
										<p className="already-sign-in pt-2">
											Already have an Account?{" "}
											<a href="#" className="text-primary signin">
												{" "}
												Sign In
											</a>
										</p>
									</div>
								</div>
								<img
									src={cancel}
									type="button"
									style={{position:'absolute', right:'10px', top:'-15px'}}
									alt="cancel"
									onClick={handleClose}
								/>
							</div>
							<Modal.Body className ="mt-n5">
								<p className="sign-up">Sign Up For Free, Now</p>

								<form onSubmit={handleSubmit(onSubmit)}>
									<div className="row g-2">
										<div className="col-3">
											<select
												name="countryCode"
												className={"form-select form-field" 
													
												}
											>
												<option value="+234">+234</option>
											</select>
										</div>
										<div className="col-9 phone-column">
											<div className="form-floating">
												<input
													type="tel"
													name="phone_number"
													value={props.phoneNumber ? ('0'+props.phoneNumber.slice(3)): data.phone_number}
													{...register('phone_number', {required: true,  minLength: 11, maxLength:11, pattern: /^[+-]?\d*(?:[.,]\d*)?$/})}
													readOnly={props.phoneNumber ? true : false}
													id="phone"	
													className={"form-control form-field " + (errors.phone_number ? "error" : " ")}  
													placeholder="Phone Number"
												/>
												{errors.phone_number && errors.phone_number.type === "required" && <span className="errorMessage">Phone number is required</span>}
      											{errors.phone_number && errors.phone_number.type === "minLength" && <span className="errorMessage">Incorrect phone number,</span> }
												{errors.phone_number && errors.phone_number.type === "maxLength" && <span className="errorMessage">Incorrect phone number</span> }
												{errors.phone_number && errors.phone_number.type === "pattern" && <span className="errorMessage">Valid phone number is required</span> } 
												<label for="floatingInput">Phone Number</label>
												
											</div>
										</div>
									</div>
									<div className="col-12 form-group">
										<div className="form-floating">
											<input
												type="text"
												name="fullname"
												{...register("fullname", {required: true})} 
												className={"form-control form-field " + (errors.fullname ? "error" : " ")}  
												placeholder="Full Name"
											/>
											{errors.fullname && errors.fullname.type === "required" && <span className="errorMessage">Your fullname is required</span>}
											<label for="floatingInput">Full Name</label>
		
										</div>
									</div>
									<div className="col-12 form-group">
										<div className="form-floating">
											<input
												type="text"
												name="email"
												{...register("email", {required: true, pattern: /^\S+@\S+$/i})}
												className={"form-control form-field " + (errors.email ? "error" : " ")}  
												placeholder="E-mail (Optional)"
											/>
											{errors.email && errors.email.type === "required" && <span className="errorMessage">email is required</span>}
											{errors.email && errors.email.type === "pattern" && <span className="errorMessage">Incorrect email format</span>}
											<label for="floatingInput">E-mail</label>
											
										</div>
									</div>
									<div className="col-12 form-group">	
										<div className="form-floating">
											<input
												type="password"
												name="password"
												{...register('password', 
													{
													required: true, 
													minLength:7,
													pattern: '', 
														
											     })}
												 className={"form-control form-field " + (errors.password ? "error" : " ")}  
												placeholder="Password"
											/>
											
											{errors.password && errors.password.type === "required" && <span className="errorMessage">Password is required</span>}
											{errors.password && errors.password.type === "minLength" && <span className="errorMessage">Password must be at least 7 long</span>}
											{errors.password && errors.password.type === "pattern" && <span className="errorMessage">Password must contain at least one special character.</span>}
											<label for="floatingInput">Password</label>

										</div>
									</div>
									<div className="col-12 form-group">
										<div className="form-floating">
											<input
												type="password"
												name="repassword"
												{...register('repassword',{
													required:true,
													validate: value => value === password.current,

												})}
												
												className={"form-control form-field " + (errors.repassword ? "error" : " ")}  		
												placeholder="Re-Type Password"
											/>
											{errors.repassword && errors.repassword.type === "required" && <span className="errorMessage">Confirm password is required</span>}
											{errors.repassword && errors.repassword.type === "validate" && <span className="errorMessage">Password does not match</span>}
											<label for="floatingInput">Re-Type PAssword</label>
										</div>
									</div>

									<div className="col-12 form-group">
										<p className="password-notes ml-3">
											<span className="text-danger">*</span> Password must be
											at least 7 long and include at least one special
											character.
										</p>
									</div>
									<div className="col-12 form-group">
										<div className=" ">
											<input
												type="checkbox"
												name="check"
												{...register('check')}
												id="check"
												className="form-check-input me-2"
											/>
											<label className="form-check-label">
												I want to be informed of special promotions and news
												regarding jeetar
											</label>
										</div>
									</div>
									<div className="col-12 form-group">
										<button
											type="submit"
											className="submit form-control btn-primary"
										>
											Create Account
										</button>
									</div>
									<div className="col-12 form-group">
										<img src={continueRule} className="d-block mx-auto" />
									</div>
									<div className="col-12 tw-flex tw-justify-center form-group text-center">
										<a href="#">
											<img src={google} className="mx-3" alt="google" />
										</a>
										<a href="#">
											<img src={facebook} className=" mx-3" alt="facebook" />
										</a>
									</div>
								</form>
							</ Modal.Body>
						</div>
					</div>
					</Modal>
			</>
		);
	}

export default SignUp;
