import React from "react";
import GridProduct from "../LoggedInHome/ProductSection/GridProduct";
import { dummyData } from "../LoggedInHome/ProductSection/DummyData";

const RecommendedProducts = () => {
	// console.log(dummyData);
	const dataArray = dummyData;
	const data = dataArray[1].subcategories[1];
	return (
		<div className="tw-mt-12 tw-mb-16">
			<p className="tw-text-jt-black jt-glb tw-text-jt-18">Recommended Products</p>

			<div
				style={{ gap: "1px" }}
				className="tw-grid tw-bg-gray-50 tw-rounded-md tw-grid-cols-5"
			>
				{data.products.map((each) => {
					return <GridProduct data={each} />;
				})}
			</div>
		</div>
	);
};

export default RecommendedProducts;
