import React, { useState } from "react";
import user from "./../logo/user.svg";
import { NavLink } from "react-router-dom";
import textlogo from "./../logo/textlogo.svg";
import jeetaryellow from "./../img/jeetaryellow.svg";
import herojeetarlogo from "./../img/herojeetarlogo.svg";
import styled from "styled-components";
import "./Navbar.css";
import SelectAddress from "./SelectAddress";
import Login from "./Login";
import SignUp from "./Signup";
import OTP from "./OTP";

const Navbar = () => {
	const [showLogin, handleShowLogin] = useState(false);
	const [showSignUp, handleSignUp] = useState(false);
	const [showOTP, handleShowOTP] = useState(false);

	const openLogin = () => {
		handleShowLogin(true);
	};
	const openSignUp = () => {
		handleSignUp(true);
	};
	const openOTP = () => {
		handleShowOTP(true);
	};

	const closeLogin = () => {
		handleShowLogin(false);
	};
	const closeSignUp = () => {
		handleSignUp(false);
	};
	const closeOTP = () => {
		handleShowOTP(false);
	};

	return (
		<>
			<nav>
				<ul className="container-fluid nav pt-3 px-2 px-md-0 py-5">
					<li className="nav-item me-sm-5">
						<NavLink
							to="/"
							exact={true}
							className="nav-link nav-left"
							activeClassName="active"
						>
							jeetar
						</NavLink>
					</li>
					<li className="nav-item me-sm-5">
						<NavLink
							to="/jeetarhealth"
							className="nav-link nav-left"
							activeClassName="active"
						>
							jeetarhealth
						</NavLink>
					</li>
					<li className="nav-item me-sm-5">
						<NavLink
							to="/jeetarbusiness"
							className="nav-link nav-left"
							activeClassName="active"
						>
							jeetarbusiness
						</NavLink>
					</li>

					<li className="d-none d-sm-block nav-item  me-2 ms-auto nav-right">
						<span exact={true} className="nav-link" onClick={openLogin}>
							Login
						</span>
					</li>
					<li className="d-none d-sm-block nav-item nav-right">
						<span exact={true} className="nav-link" onClick={openSignUp}>
							<img src={user} className="d-inline nav__userIcon" /> Sign Up
						</span>
					</li>
				</ul>
				<div className="header-image d-flex justify-content-center align-items-center py-3 d-block d-sm-none">
					<img src={herojeetarlogo} className="img-fluid w-50" />
				</div>
			</nav>
			<Login status={showLogin} close={closeLogin} open={openLogin} otpFunc={openOTP} />
			<SignUp status={showSignUp} close={closeSignUp} open={openSignUp} />
			<OTP status={showOTP} close={closeOTP} open={openOTP} />

			<SelectAddress />
		</>
	);
};

export default Navbar;
