import { FiPlus } from 'react-icons/fi';
/*import add from '../img/add.svg'; */
import React, {useState} from 'react';
import { Button, Modal } from "react-bootstrap";
import Login from '../Login';
import LazyLoad from 'react-lazyload';
import plus from '../../img/plus.svg';
import add from '../../img/add.svg';
import deleteIcon from '../../img/Delete.svg';
import cancel from "../../logo/cancel.svg";
import '../../component/Product.css';
import Heart from '../../img/Heart.svg'
import { Link } from 'react-router-dom';
import CartButtonsVertical from '../../pages/LoggedInPages/LoggedInHome/ProductSection/CartButtonsVertical';
import ProductModal from '../ProductModal';

export default function ProductItem({ product }) {
    const [isModalOpen, setIsModalOpen] = useState(false);

	const closeModal = () => {
		setIsModalOpen(false);
	};

	const openModal = () => {
		setIsModalOpen(true);
	};

    const [showLogin, handleShowLogin] = useState(false);

	const openLogin = () => {
		handleShowLogin(true);
	};

    const closeLogin = () => {
		handleShowLogin(false);
	};

    const [showOTP, handleShowOTP] = useState(false);

    const openOTP = () => {
		handleShowOTP(true);
	};

    const closeOTP = () => {
		handleShowOTP(false);
	};



    const itemData ={
        id:product.id,
		name:product.title,
		//sku: product.weight,
		price: product.retail_price,
        weight:product.weight,
		description: product.description,
        imgsrc: product.image,
    }
    return (
        <>
        <Login status={showLogin} close={closeLogin} open={openLogin} otpFunc={openOTP} />
        <ProductModal data={itemData} show={isModalOpen} handleClose={closeModal} />
        
        <div className="bg-white flex flex-col gap-y-1 relative items-center text-center text-sm font-semibold p-3 tw-border-gray-100">
            
            <button className="absolute top-3 right-3 bg-white w-8 h-8 flex items-center justify-center border border-gray-200 rounded-lg transition-colors text-brand-color shadow-md hover:border-brand-color">
              <img src={add} className="block tw-mx-auto" onClick={openLogin}/>
            </button>
            <div className="tw-cursor-pointer color-b" onClick={openModal}>
                <img src={product.image} alt={product.title} />
                <div className="price">&#8358;{product.retail_price}</div>
                <div className="name">{product.title}</div>
                <div className="weigth">{product.weight}</div>
            </div>
           
        </div>
        </>
    )
}
