import React, { useRef, useState ,useEffect} from "react";
import { useForm } from "react-hook-form";
import axios from "axios";
import { Modal } from 'react-bootstrap';
import OTP from "./OTP";
import styled from "styled-components";
import PasswordModal from "./PasswordModal";
import cancel from "./../logo/cancel.svg";
import backArrow from "../img/backArrow.svg";




const Register = styled.p`
	font-family: Gilmer Bold;
	font-style: normal;
	font-weight: bold;
	font-size: 18px;
	line-height: 22px;
	text-align: center;
	margin-buttom: 10px;
	color: #374b58;
`;

const Login = (props) => {

	
	const {
		register,
		handleSubmit,
		formState: { errors },
	  } = useForm({mode: 'all'});

	  const handleClose = () => {
		props.close();
	};
	  
	  const [showOTP, handleShowOTP] = useState(false)
	  const openOTP =()=>{handleShowOTP(true)}	
	  const closeOTP =()=>{handleShowOTP(false)}
	  
	  const [phoneNumber, setphoneNumber] = useState(null);
	  const [token, setToken] = useState(null);
	  const [message, setMessage]=useState(null)
	  const [loading, setLoading]=useState(false)

	  const [showPasswordModal, handleShowPasswordModal] = useState(false)
	  const openPasswordModal =()=>{handleShowPasswordModal(true)}	
      const closePasswordModal =()=>{handleShowPasswordModal(false)}

	  
	  
	  useEffect(() => {
		//console.log(phoneNumber, token);
		if(phoneNumber){
		  registerUser();
		}
	  }, [phoneNumber])


	  const registerUser = async () =>{
		setLoading(true)
		 try{
	 
		   await axios.post(`https://jeetar-eqtxqyoupa-nw.a.run.app/api/v1/auth/register`, { phone_number: phoneNumber})
		   .then(res => {
					 console.log(res.data.message)
					 setToken(res.data.data.temp_token);
					 setLoading(false)
					 openOTP();
		   }).catch(
			error =>{
				setLoading(false);
				if(error.response){
				  setToken(null);
				  const status =error.response.data.status;
				  if(error.response.data.error.temp_token){
					setToken(error.response.data.error.temp_token);
					openOTP();
				  }
				  else{
					openPasswordModal();
				  }
				  
				   console.log(error.response.data)
				}
			  }
			  
		   );
		 }
		 catch(err){
		   setLoading(false)
		   console.log(err);
		 }
	   }
	  
	//const [OTP, setOTP] = useState(data.phone)  

	  // const [login, hideLogin] = useState(props.status);
	  // const [show, setShow] = useState(false);
	  // const handleClose = () => setShow(false); 
	  // const handleShow = () => setShow(true);
	  // const removeLogin =()=>hideLogin(false);

	  //const baseUrl = "https://jeetar-eqtxqyoupa-nw.a.run.app/api/v1/";

	  
	  const onSubmit = async data => {
		if (data) {
			let phone = "234" + data.phone_number.substring(1);
			setphoneNumber(null)
			setphoneNumber(phone);   
			// setTimeout(() => {
			//   registerUser();  
			// }, 5000);
		  }
		  
	  }
	  
	  
	return (
		<> 
		<PasswordModal status ={showPasswordModal} close={closePasswordModal} open={openPasswordModal} phoneNumber ={phoneNumber} token ={token} message={message} /> 
		 <OTP status ={showOTP} close={closeOTP} open={openOTP} phoneNumber ={phoneNumber} token ={token} message={message} />   
		 <Modal show={props.status} >
		        <Modal.Header className="border-0 tw-py-2" id="modal__header">
					<img
						src={backArrow}
						alt="arror_back"
						className="d-block-inline arrow_back"
						onClick={handleClose}
					/>
					<p className="one__time__password tw-text-center">Login with Phone</p>

					<img src={cancel} className="login-close" alt="cancel" onClick={handleClose} />
				</Modal.Header>
				<Modal.Body>
				<form onSubmit={handleSubmit(onSubmit)}>
						<div className="row g-2">
							<div className="col-3">
								<select
									name="countryCode"
									className={"form-select form-field" 
										
									}
								>
									<option value="+234">+234</option>
								</select>
							</div>
							<div className="col-9 phone-column">
								<div className="form-floating">
									<input
										type="tel"
										name="phone_number"
										{...register('phone_number', {required: true,  minLength: 11, maxLength:11, pattern: /^[+-]?\d*(?:[.,]\d*)?$/})}
										id="phone"	
										className={"form-control form-field " + (errors.phone_number ? "error" : " ")}  
										placeholder="Phone Number"
									/>
									{errors.phone_number && errors.phone_number.type === "required" && <span className="errorMessage">Phone number is required</span>}
									{errors.phone_number && errors.phone_number.type === "minLength" && <span className="errorMessage">Incorrect phone number,</span> }
									{errors.phone_number && errors.phone_number.type === "maxLength" && <span className="errorMessage">Incorrect phone number</span> }
									{errors.phone_number && errors.phone_number.type === "pattern" && <span className="errorMessage">Valid phone number is required</span> }
										
									<label for="floatingInput">Phone Number</label>
									
								</div>
							</div>
						</div>
						
						<div className="col-12 form-group">
							<button
								type="submit"
								className="submit form-control btn-primary"
							>
								Continue With Phone Number
							</button>
						</div>
						
					</form>
				     </Modal.Body>		
			   </Modal> 		   
		</>
	)
  }
export default Login;
